
export class Color {

    static rgbArray(d: string[]) {
        if (d.length === 0) { return null; }
        let rtn = [];

        for (let i = 0, c, p; i < d.length; i++) {
            if (d[i].length < 6) { continue; }
            c = d[i];		// Just an alias(copy really) of the color text, make code smaller.
            p = (c[0] === '#') ? 1 : 0;	// Determine starting position in char array to start pulling from

            rtn.push(
                parseInt(c[p] + c[p + 1], 16) / 255.0,
                parseInt(c[p + 2] + c[p + 3], 16) / 255.0,
                parseInt(c[p + 4] + c[p + 5], 16) / 255.0
            );
        }
        return rtn;
    }
    constructor() {
        //
    }

}